import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"

import './CardComponent.css'
import { Col, Row } from "antd/es/grid";
import { FormOutlined } from '@ant-design/icons';
import { DownOutlined } from '@ant-design/icons';
import { Button, Dropdown, Image, Space } from 'antd';
import { TagCloud } from 'react-tagcloud'
const data = [

  { value: 'Express.js', count: 25 },
  { value: 'HTML5', count: 33 },
  { value: 'MongoDB', count: 18 },
  { value: 'CSS3', count: 20 },
  { value: 'JavaScript', count: 38 },
  { value: 'React', count: 30 },
  { value: 'Nodejs', count: 28 },
  { value: 'Express.js', count: 25 },
  { value: 'HTML5', count: 33 },
  { value: 'MongoDB', count: 18 },
  { value: 'CSS3', count: 20 },
  { value: 'JavaScript', count: 38 },
  { value: 'React', count: 30 },
  { value: 'Nodejs', count: 28 },
  { value: 'Express.js', count: 25 },
  { value: 'HTML5', count: 33 },
  { value: 'MongoDB', count: 18 },
  { value: 'CSS3', count: 20 },
]


class OnDisplayData extends StreamlitComponentBase<any> {
  public state = { selectedTopic: 0 }

  

  public render = (): ReactNode => {
    console.log(this.props.args)
    const embedList = this.props.args["menu"]
    const group_names = this.props.args["group_names"]
    const text_groups = this.props.args["text_groups"]
    const summaries = this.props.args["summaries"]
    const topics = this.props.args["topics"]
    const generated_number_groups = this.props.args["generated_number_groups"]
    const word_cloud_image = Buffer.from(this.props.args["word_cloud_image"], "base64").toString()
    const childArr:any=embedList.map((item:any)=>{
      return {
        key: item,
        label: item,
      }
    })

    const selectedTopicsOptions:any = [];
    for(let i=0; i<generated_number_groups; i++){
      selectedTopicsOptions.push({
        label: (i+1).toString()+"/"+generated_number_groups,
        key: i,
      },)
    }

    return (
      <div className="dashboardParentDiv" >
        <Row  className="NameGroup">
          <div style={{display:"flex"}}>
          <p className="groupNameHeading">{group_names[this.state.selectedTopic]}</p>
          <FormOutlined style={{width:"18px", marginLeft:"10px" , paddingTop:"16px"}} />
          </div>
          <div style={{display:"flex"}}>
         
              <Button className="uploadButton"><svg  width="12" height="11" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M9.99984 8.00008V10.0001H1.99984V8.00008H0.666504V10.0001C0.666504 10.7334 1.2665 11.3334 1.99984 11.3334H9.99984C10.7332 11.3334 11.3332 10.7334 11.3332 10.0001V8.00008H9.99984ZM2.6665 4.00008L3.6065 4.94008L5.33317 3.22008V8.66675H6.6665V3.22008L8.39317 4.94008L9.33317 4.00008L5.99984 0.666748L2.6665 4.00008Z" fill="#888C9C"/>
                </svg>
              </Button>

              <Dropdown overlayStyle={{background:"red !important"}} menu={{ items:[
             
                {
                  key: '1',
                  label: 'Embadded Model Name',
                  children: childArr,
                },
                {
                  key: '2',
                  type: 'group',
                  children: [
                    {
                      key: '2-1',
                      label: 'Temperature',
                    },
                    {
                      key: '2-2',
                      label: 'Similarity_Score ',
                      // danger: true,
                      
                    }
                  ],
                },
              ], }} placement="top">
                <Button className="settingButton"><svg width="13" height="13" viewBox="0 0 13 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M6.5 8.53125C5.37875 8.53125 4.46875 7.62125 4.46875 6.5C4.46875 5.37875 5.37875 4.46875 6.5 4.46875C7.62125 4.46875 8.53125 5.37875 8.53125 6.5C8.53125 7.62125 7.62125 8.53125 6.5 8.53125ZM6.5 5.28125C5.82833 5.28125 5.28125 5.82833 5.28125 6.5C5.28125 7.17167 5.82833 7.71875 6.5 7.71875C7.17167 7.71875 7.71875 7.17167 7.71875 6.5C7.71875 5.82833 7.17167 5.28125 6.5 5.28125Z" fill="white"/>
                  <path d="M8.23891 12.0197C8.12516 12.0197 8.01141 12.0034 7.89766 11.9763C7.56183 11.8842 7.28016 11.673 7.10141 11.3751L7.03641 11.2667C6.71683 10.7142 6.27808 10.7142 5.9585 11.2667L5.89891 11.3697C5.72016 11.673 5.4385 11.8897 5.10266 11.9763C4.76141 12.0684 4.40933 12.0197 4.11141 11.8409L3.17975 11.3047C2.84933 11.1151 2.611 10.8063 2.50808 10.4326C2.41058 10.0588 2.45933 9.67424 2.64891 9.34382C2.806 9.06757 2.84933 8.81841 2.75725 8.66132C2.66516 8.50424 2.43225 8.41216 2.11266 8.41216C1.32183 8.41216 0.677246 7.76757 0.677246 6.97674V6.02341C0.677246 5.23257 1.32183 4.58799 2.11266 4.58799C2.43225 4.58799 2.66516 4.49591 2.75725 4.33882C2.84933 4.18174 2.81141 3.93257 2.64891 3.65632C2.45933 3.32591 2.41058 2.93591 2.50808 2.56757C2.60558 2.19382 2.84391 1.88507 3.17975 1.69549L4.11683 1.15924C4.72891 0.796324 5.536 1.00757 5.90433 1.63049L5.96933 1.73882C6.28891 2.29132 6.72766 2.29132 7.04725 1.73882L7.10683 1.63591C7.47516 1.00757 8.28225 0.796324 8.89975 1.16466L9.83141 1.70091C10.1618 1.89049 10.4002 2.19924 10.5031 2.57299C10.6006 2.94674 10.5518 3.33132 10.3622 3.66174C10.2052 3.93799 10.1618 4.18716 10.2539 4.34424C10.346 4.50132 10.5789 4.59341 10.8985 4.59341C11.6893 4.59341 12.3339 5.23799 12.3339 6.02882V6.98216C12.3339 7.77299 11.6893 8.41757 10.8985 8.41757C10.5789 8.41757 10.346 8.50966 10.2539 8.66674C10.1618 8.82382 10.1997 9.07299 10.3622 9.34924C10.5518 9.67966 10.606 10.0697 10.5031 10.438C10.4056 10.8117 10.1672 11.1205 9.83141 11.3101L8.89433 11.8463C8.6885 11.9601 8.46641 12.0197 8.23891 12.0197ZM6.50016 10.0155C6.98225 10.0155 7.43183 10.3188 7.74058 10.8551L7.80016 10.958C7.86516 11.0717 7.9735 11.153 8.1035 11.1855C8.2335 11.218 8.3635 11.2017 8.47183 11.1367L9.40891 10.5951C9.54975 10.5138 9.65808 10.3784 9.70141 10.2159C9.74475 10.0534 9.72308 9.88549 9.64183 9.74466C9.33308 9.21382 9.29516 8.66674 9.5335 8.24966C9.77183 7.83257 10.2647 7.59424 10.8822 7.59424C11.2289 7.59424 11.5052 7.31799 11.5052 6.97132V6.01799C11.5052 5.67674 11.2289 5.39507 10.8822 5.39507C10.2647 5.39507 9.77183 5.15674 9.5335 4.73966C9.29516 4.32257 9.33308 3.77549 9.64183 3.24466C9.72308 3.10382 9.74475 2.93591 9.70141 2.77341C9.65808 2.61091 9.55516 2.48091 9.41433 2.39424L8.47725 1.85799C8.24433 1.71716 7.93558 1.79841 7.79475 2.03674L7.73516 2.13966C7.42641 2.67591 6.97683 2.97924 6.49475 2.97924C6.01266 2.97924 5.56308 2.67591 5.25433 2.13966L5.19475 2.03132C5.05933 1.80382 4.756 1.72257 4.52308 1.85799L3.586 2.39966C3.44516 2.48091 3.33683 2.61632 3.2935 2.77882C3.25016 2.94132 3.27183 3.10924 3.35308 3.25007C3.66183 3.78091 3.69975 4.32799 3.46141 4.74507C3.22308 5.16216 2.73016 5.40049 2.11266 5.40049C1.766 5.40049 1.48975 5.67674 1.48975 6.02341V6.97674C1.48975 7.31799 1.766 7.59966 2.11266 7.59966C2.73016 7.59966 3.22308 7.83799 3.46141 8.25507C3.69975 8.67216 3.66183 9.21924 3.35308 9.75007C3.27183 9.89091 3.25016 10.0588 3.2935 10.2213C3.33683 10.3838 3.43975 10.5138 3.58058 10.6005L4.51766 11.1367C4.63141 11.2072 4.76683 11.2234 4.89141 11.1909C5.02141 11.1584 5.12975 11.0717 5.20016 10.958L5.25975 10.8551C5.5685 10.3242 6.01808 10.0155 6.50016 10.0155Z" fill="white"/>
                  </svg>
              </Button>
            </Dropdown>
            <Button className="CSVButton">Download CSV</Button>
          </div>
        </Row>
        <Row className="summarySection">
          <Col span={22} >
            <p className="summary">Summary</p>
            </Col>
            <Col span={2}>
              <div className="similarity  p-2 m-2" style={{background:'#262730', borderRadius:5, textAlign:'center'}}>{+this.state.selectedTopic+1}/{generated_number_groups}</div>
            </Col>
            <p className="summaryParagraph setScroll">{summaries[this.state.selectedTopic]}</p>
        </Row>
        <Row className="mt-3">
          <Col span={12} className="topicCard">
            <p className="topics">Topics</p>
            {/* <img src={word_cloud_image} alt="wordcloud"/> */}
            {/* <p>{topics[this.state.selectedTopic]}</p> */}
            {/* <TagCloud
              minSize={12}
              maxSize={35}
              tags={data}
              onClick={(tag:any) => alert(`'${tag.value}' was selected!`)}
            /> */}
            <Image src={`data:image/jpg;base64,${topics[this.state.selectedTopic]}`} alt="wordcloud"/>
          </Col>
          <Col   offset={1} span={11} className="topicCard">
            <p className="similerText">Similer Text</p>
            <ul className="setScroll" >
              {
                text_groups[this.state.selectedTopic].map((item:any)=>{
                  return(
<li className="similerTextParagraph">{item}</li>
                  )
                })
              }
              
   
              </ul>
          </Col>
        </Row>
        <Row className="mt-1 mb-1 p-5 d-flex justify-content-center">
          <Button  className={this.state.selectedTopic>0?"NextButton":"previousButton"} onClick={this.onPreviousClick}>Previous</Button>
          <Dropdown  className="customDropdown"  menu={{
            items:selectedTopicsOptions,
            onClick: this.handleMenuClick,
            }}>
              <Button className="dropdownButton" >
                <Space>
                  {`${+this.state.selectedTopic+1}/${generated_number_groups}`}
                  <DownOutlined />
                </Space>
              </Button>
          </Dropdown >
          <Button  className={(this.state.selectedTopic<generated_number_groups-1)?"NextButton":"previousButton"} onClick={()=>this.onNextClick(generated_number_groups)}>Next</Button>
        </Row>
      </div>
    )
  }

  private handleMenuClick = (e:any): void => {
    this.setState({selectedTopic:e.key})
    // Streamlit.setComponentValue(e.key)
  }
  private onPreviousClick = (): void => {
    if(this.state.selectedTopic>0){
      this.setState((prev:any)=>({selectedTopic:prev.selectedTopic-1}))
    }
  }
  private onNextClick = (end:number): void => {
    if(this.state.selectedTopic<end-1)
    this.setState((prev:any)=>({selectedTopic:prev.selectedTopic+1}))
  }
}

export default withStreamlitConnection(OnDisplayData)
